import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MainFeaturedPost from '../../blog/MainFeaturedPost';
import FeaturedPost from '../../blog/FeaturedPost';
import Main from '../../blog/Main';
import Sidebar from '../../blog/Sidebar';
import post1 from '../../blog/blog-post.1.md';
import post2 from '../../blog/blog-post.2.md';
import post3 from '../../blog/blog-post.3.md';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

const mainFeaturedPost = {
  title: 'Metodo de Biseccion',
  description:
    "Multiple lines of text that form the lede, informing new readers quickly and efficiently about what's most interesting in this post's contents.",
  image: 'https://source.unsplash.com/random',
  imgText: 'main image description',
  linkText: 'Continue reading…',
};

const featuredPosts = [
  {
    title: 'Metodo de Euler',
    date: 'Nov 12',
    description:
      'This is a wider card with supporting text below as a natural lead-in to additional content.',
    image: 'https://source.unsplash.com/random',
    imageText: 'Image Text',
    url: '/metodo-de-euler'
  },
  {
    title: 'Metodo de Punto Fijo',
    date: 'Nov 11',
    description:
    'This is a wider card with supporting text below as a natural lead-in to additional content.',
    image: 'https://source.unsplash.com/random',
    imageText: 'Image Text',
    url: '/metodo-de-punto-fijo'
  },
];

const posts = [post1, post2, post3];

const sidebar = {
  title: 'Informacion',
  description:
    'Etiam porta sem malesuada magna mollis euismod. Cras mattis consectetur purus sit amet fermentum. Aenean lacinia bibendum nulla sed consectetur.',
  archives: [
    { title: 'Metodo de Biseccion', url: '/metodo-de-biseccion' },
    { title: 'Metodo de Punto Fijo', url: '/metodo-de-punto-fijo' },
    { title: 'Metodo de Newton-Raphson', url: '/metodo-de-newton-raphson' },
    { title: 'Metodo de Euler', url: '/metodo-de-euler' },
  ],
};

const useStyles = makeStyles(theme => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));

export default function HomePage() {
  const classes = useStyles();

  return (
    <Container maxWidth='lg'>
      <main>
        <MainFeaturedPost post={mainFeaturedPost} />
        <Grid container spacing={4}>
          {featuredPosts.map(post => (
            <FeaturedPost key={post.title} post={post} />
          ))}
        </Grid>
        <Grid container spacing={5} className={classes.mainGrid}>
          <Main title='Calculo Numerico y sus Metodos' posts={posts} />
          <Sidebar
            title={sidebar.title}
            description={sidebar.description}
            archives={sidebar.archives}
          />
        </Grid>
      </main>
    </Container>
  );
}
