import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from './Header';
import Footer from './Footer';
import HomePage from '../views/home/Home';
import { Route, Switch } from 'react-router-dom';

const sections = [
  { title: 'Metodo de Biseccion', url: 'metodo-de-biseccion' },
  { title: 'Metodo de Punto Fijo', url: 'metodo-de-punto-fijo' },
  { title: 'Metodo de Newton-Raphson', url: 'metodo-de-newton-raphson' },
  { title: 'Metodo de Euler', url: 'metodo-de-euler' },
];

const RouterTopic = ({data}) => (<div><h2>{data}</h2></div>);

export default function Blog() {

  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth='lg'>
        <Header title='Blog' sections={sections} />
      </Container>
      <Switch>
        <Route exact path='/' component={HomePage} />
        <Route exact path='/metodo-de-biseccion' render={() => <RouterTopic data='Biseccion'/>}/>
        <Route exact path='/metodo-de-punto-fijo' render={() => <RouterTopic data='Punto Fijo'/>}/>
        <Route exact path='/metodo-de-euler' render={() => <RouterTopic data='Euler'/>}/>
        <Route exact path='/metodo-de-newton-raphson' render={() => <RouterTopic data='Newton'/>}/>
      </Switch>
      <Footer
        title='Fred Zerpa'
        description='Un blog sobre Calculo Numerico.'
      />
    </React.Fragment>
  );
}
